.shopping-list-text {
  max-width: 244px;
  text-align: center;
  letter-spacing: -.02em;
  color: rgba(0, 0, 0, .6);
  margin: 120px auto 14px;
  font-weight: 400;
  line-height: 1.28571;
}

@media only screen and (min-width: 768px) {
  .shopping-list-text {
    max-width: 349px;
    margin-top: 140px;
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 1.33333;
  }
}

.shopping-list-image {
  margin: 0 auto;
}

.shop-cart-list {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.shop-cart-container {
  background-color: var(--white-color);
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  padding: 14px;
}

@media only screen and (min-width: 768px) {
  .shop-cart-container {
    padding: 24px;
  }
}

.shop-cart-wrap {
  gap: 14px;
  display: flex;
  position: relative;
}

@media only screen and (min-width: 768px) {
  .shop-cart-wrap {
    gap: 24px;
  }
}

.shop-image-wrapper {
  max-width: 100px;
  max-height: 142px;
  border-radius: 8px;
  overflow: hidden;
}

@media only screen and (min-width: 768px) {
  .shop-image-wrapper {
    max-width: 116px;
    max-height: 165px;
  }
}

.shop-cart-info {
  flex-grow: 1;
}

.shop-cart-title {
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: var(--light-black-color);
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.shop-cart-category {
  letter-spacing: .03em;
  color: var(--grey-text-color);
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
}

.shop-cart-description {
  font-style: 14px;
  letter-spacing: -.04em;
  min-height: 68px;
  margin-bottom: 14px;
  font-weight: 400;
  line-height: 1.28571;
}

@media only screen and (min-width: 768px) {
  .shop-cart-description {
    min-height: 53px;
    margin-bottom: 48px;
  }
}

.shop-cart-bottom-wrap {
  justify-content: space-between;
  display: flex;
}

.shop-cart-author {
  letter-spacing: -.04em;
  color: var(--grey-text-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16667;
}

.shop-cart-media {
  max-width: 80px;
  gap: 8px;
  display: flex;
}

@media only screen and (min-width: 768px) {
  .shop-cart-media {
    max-width: 140px;
    gap: 16px;
  }
}

.amazon-icon {
  padding-top: 3px;
}

.media-icon {
  transition-property: transform, opacity;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--timing-function);
}

.media-icon:hover {
  mix-blend-mode: luminosity;
  filter: drop-shadow(0 3px 3px rgba(0, 0, 0, .15));
  transform: translateY(-2px)scale(1.1);
}

.shop-cart-btn {
  width: 28px;
  height: 28px;
  background-color: var(--blue-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

@media only screen and (min-width: 768px) {
  .shop-cart-btn {
    width: 34px;
    height: 34px;
  }
}

.shop-cart-btn-trash {
  fill: rgba(0, 0, 0, 0);
  stroke: #fff;
  width: 16px;
  height: 16px;
}

@media only screen and (min-width: 768px) {
  .shop-cart-btn-trash {
    width: 18px;
    height: 18px;
  }
}

.shop-cart-btn {
  transition-property: transform;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--timing-function);
}

.shop-cart-btn:hover {
  mix-blend-mode: luminosity;
  filter: drop-shadow(0 3px 3px rgba(0, 0, 0, .15));
  transform: scale(1.1);
}

.dark-theme {
  background-color: var(--dark-grey-color);
  color: var(--white-color);
}

.dark-theme .header-container {
  background-color: var(--black-color);
}

.dark-theme .bookshelf-logo {
  fill: var(--white-color);
}

.dark-theme .menu-burger-icon-open {
  stroke: var(--white-color);
}

.dark-theme .header-nav-link {
  color: var(--white-color);
  fill: var(--white-color);
}

.dark-theme .current {
  color: var(--black-color);
}

.dark-theme .header-container {
  border: 1.5px solid var(--white-color);
}





